@import "https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;500;700&display=swap";
:root {
  --active-link-color: #00acdc;
}

a {
  margin: 0;
  padding: 0;
  line-height: 0;
  display: inline-block;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 6.5px 50px;
  display: flex;
}

.header__icon {
  width: 40px;
  height: 40px;
}

.header__icon img {
  display: block;
}

.header__list {
  align-items: center;
  height: 15px;
  font-weight: 500;
  display: flex;
}

.header__list_item {
  background-color: #000;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  text-decoration: none;
  list-style: none;
  display: flex;
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-link-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: -22px;
  left: 0;
}

.header__list_item:last-child {
  margin-right: 0;
}

.header__list_item a {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  text-decoration: none;
  transition: all .3s;
}

.is-active a, .header__list_item a:hover {
  color: var(--active-link-color);
}
